.enter {
	opacity: 0;
}

.enterActive {
	opacity: 1;
	transition-duration:  0.3s;
	transition-property: opacity;
	transition-timing-function: cubic-bezier(0.54, 1.12, 0.38, 1.11);
}

.exit {
	opacity: 1;
}

.exitActive {
	opacity: 0;
	transition-duration: 0.1s;
	transition-property: opacity;
	transition-timing-function: cubic-bezier(0.4, 1, 0.75, 0.9);
}
